// vue,thisvue,
// store,;

// axios
import http from '@/api/common/http.js'

// url
import {
	url_login, // # 
	url_bindMobile, // # 
} from '@/api/common/http_req_list.js'

// 
// import {
// 	httpResOk
// } from '@/api/common/config.js'

// # 
function http_login(data) {
  return http({
    url: url_login,
    method: 'post',
    data
  })
}

// # 
function http_bindMobile(data) {
  return http({
    url: url_bindMobile,
    method: 'post',
    data
  })
}

export default {
	http_login,
	http_bindMobile,
}
