import axios from '@/api/common/js_sdk/gangdiedao-uni-axios/index.js'



// vue,thisvue,
// store,;
import store from '@/store/index.js';


import {baseUrl} from '@/api/common/http_req_list.js'


/**
 * 
 */
function _reqlog(req) {
    if (process.env.NODE_ENV === 'development') {
        console.log("" + req.url, req.data || req.params)
    }
    //TODO 
}


/**
 * 
 */
function _reslog(res) {
    if (process.env.NODE_ENV === 'development') {
        console.log(`${res.config.url}`, res)
    }
}


// 
const http = axios.create({
	
	  // ,,
    // baseURL: baseUrl,
		
    timeout: 6000,  //  manifest.json  networkTimeout



		headers: {
		    'Content-Type': 'application/json; charset=utf-8'
		}
})

/**
 * ,  &  & token
 */
http.interceptors.request.use(config => {

	
	// get
	// if (config.method == 'get') {
	//     config.data = 'true'
	// }
	
	// token,;
  config.headers['token'] = store.getters.store_token || ''
  return config
}, error => {
  return Promise.reject(error)
})

export default http
